<template>
  <div class="loading">
    <i class="fas fa-spinner fa-5x fa-spin"></i>
  </div>
</template>

<script>

  import env from '../env.js';
  import store from "@/store";

  export default {

    data() {
      return {
        //baseUrl: env.provider_url,
        //groupId: '00gwb2x6jewAvwYSX0h7',
        client_id: env.client_id,
        redirect_uri: env.redirect_uri,
        authorize_endpoint: env.url_authorize,
        token_endpoint: env.token_endpoint,
        userinfo_endpoint: env.url_user_info,
        scope: 'openid profile email eduperson_entitlement',

        state: undefined,
        session_state: undefined,
        code: undefined
      }
    },

    methods: {

      async startPkce() {
        const state = this.generateRandomString();
        const code_verifier = this.generateRandomString();
        localStorage.state = state;
        localStorage.code_verifier = code_verifier;
        const code_challenge = await this.pkceChallengeFromVerifier(code_verifier);
        const params = {
          response_type: 'code',
          client_id:  this.client_id,
          state,
          scope: 'openid profile email eduperson_entitlement',
          redirect_uri:  location.origin+'/callback',
          code_challenge,
          code_challenge_method: 'S256'
        };
        location = this.authorize_endpoint + '?' + new URLSearchParams(params);
    },

      generateRandomString() {
        const array = new Uint32Array(28);
        crypto.getRandomValues(array);
        return Array.from(array, dec => ('0' + dec.toString(16)).substr(-2)).join('');
    },

      async  pkceChallengeFromVerifier(v) {
        const encoder = new TextEncoder();
        const data = encoder.encode(v);
        const hashed = await crypto.subtle.digest('SHA-256', data);
        return this.base64urlencode(hashed);
    },

      base64urlencode(str) {
        return btoa(String.fromCharCode.apply(null, new Uint8Array(str))).replace(/\+/g, '-').replace(/\//g, '_').replace(/=+$/, '');
    },

      async getUsers() {
        let tokens = await this.getTokens();
        history.replaceState({}, null, '/callback'); // Remove the auth code from the browser address bar.
        // const id_token = JSON.parse(atob(tokens.id_token.split('.')[1]));
        const access_token = JSON.parse(atob(tokens.access_token.split('.')[1]));
        const options = { headers: {Authorization: `Bearer ${tokens.access_token}` } };
        const r = await fetch(`${this.userinfo_endpoint}`, options);
        const users = await r.json();
        localStorage.setItem("session", JSON.stringify({ user: { 'access_token': tokens['access_token'], info:users, authenticated: true } }));
        store.state.isLogged = true

        let redirect = sessionStorage.getItem("redirect");
        if(redirect){
          sessionStorage.removeItem("redirect");
          window.location.href = window.location.origin + redirect
        }
        else window.location.href = window.location.origin + "/select-option"
    },

      async getTokens() {
        const params = {
          grant_type: 'authorization_code',
          code: this.code,
          client_id: this.client_id,
          redirect_uri: this.redirect_uri,
          code_verifier: localStorage.code_verifier
        };
        const r = await fetch(this.token_endpoint, {
          method: 'post',
          body: new URLSearchParams(params)
        });
        const tokens = await r.json();
        //const id_token = JSON.parse(atob(tokens.id_token.split('.')[1]));
        //const access_token = JSON.parse(atob(tokens.access_token.split('.')[1]));
        return tokens;
      }

    },

    created() {

      const query = this.$route.query
      this.code = query.code;
      this.state = query.state;
      this.session_state = query.session_state;

      if (query.error) {
        console.log(query.error)
        console.log(query.error_description)
      }
      else if (query.code) { // If the server returned an authorization code, exchange it for an access token.
        if (localStorage.getItem('state') === query.state) this.getUsers();
        else console.log('Invalid state')
      }
      else this.startPkce();
    }
  }
</script>

<style scoped>
  .loading{
    text-align: center;
    padding: max(1em, calc(20vh - 40px)) 0;
  }
</style>
